<template>
  <div style="width: 100%;margin-top: 47px">
    <spinner :data="spinnerData"></spinner>
    <div class="live liveScoresBox" style="background-color: rgba(0,0,0,.7);background-image:url('/image/298203.jpg'); background-blend-mode: overlay;background-size: cover;background-position:center center;background-repeat: no-repeat">
      <div class="versus">
        <div class="match_options">
          <span class="timing w-100 text-center">{{ getSelectedLiveFeeds.match_date ? formatDate(getSelectedLiveFeeds.match_date) : formatDate(match_date) }}</span>
          <span class="league w-100 text-center" v-html="league_name"></span>
          <span class="status w-100 text-center">{{ getSelectedLiveFeeds.match_status ? getSelectedLiveFeeds.match_status : match_status }}</span>
        </div>
        <div class="container">
          <div class="row" style="border:solid 1px transparent;">
            <div class="col col-4">
              <div v-if="getSelectedLiveFeeds.home_team_badge" class="versus-img">
                <img :src="getSelectedLiveFeeds.home_team_badge" />
              </div>
              <div v-else class="versus-img">
                <img :src="home_logo" />
              </div>
              <p v-if="getSelectedLiveFeeds.home_team_name" v-html="getSelectedLiveFeeds.home_team_name"></p>
              <p v-else v-html="home_team_name"></p>
              <div class="scorers" v-if="goalscorer">
                <p v-for="(scorers,index) in goalscorer" :key="index">
                  {{ scorers.home_scorer !== '' ? `(${scorers.time})` : '' }} {{ scorers.home_scorer }}
                </p>
              </div>
            </div>

            <div class="col col-4">
              <div class="caption">
                <p>( {{ getSelectedLiveFeeds.hometeam_score ? getSelectedLiveFeeds.hometeam_score : hometeam_score }} - {{getSelectedLiveFeeds.awayteam_score ? getSelectedLiveFeeds.awayteam_score : awayteam_score }} )</p>
                <h1>VS</h1>
              </div>
            </div>

            <div class="col col-4">
              <div v-if="getSelectedLiveFeeds.away_team_badge" class="versus-img">
                <img :src="getSelectedLiveFeeds.away_team_badge" />
              </div>
              <div v-else class="versus-img">
                <img :src="away_logo" />
              </div>
              <p v-if="getSelectedLiveFeeds.away_team_name" v-html="getSelectedLiveFeeds.away_team_name"></p>
              <p v-else v-html="away_team_name"></p>
              <div class="scorers" v-if="goalscorer">
                <p v-for="(scorers,index) in goalscorer" :key="index">
                  {{ scorers.away_scorer !== '' ? `(${scorers.time})` : '' }} {{ scorers.away_scorer }}
                </p>
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
    <div class="content-tabs">
      <div id="tabmenu">
        <ul class="tabs">
          <li class="tab-link current" data-tab="reviews">REVIEWS</li>
          <li class="tab-link" data-tab="lineups">LINEUPS</li>
          <li class="tab-link" data-tab="stats">STATS</li>
        </ul>

        <div id="reviews" class="tab-content current">
<!--          <all-feeds :feeds="reviews.data" :feed-status="feedsStatus" :feeds-count="reviews.fullFeedsCount"></all-feeds>-->
          <all-feeds :feeds="reviews.data" :feed-status="feedsStatus"></all-feeds>
        </div>
        <div id="lineups" class="tab-content">
          <div class="line-up-row ">
            <div class="line-up-card br">
              <div v-if="getSelectedLiveFeeds.home_team_badge">
                <img :src="getSelectedLiveFeeds.home_team_badge" />
              </div>
              <div v-else>
                <img :src="home_logo" />
              </div>
              <h1 v-if="getSelectedLiveFeeds.home_team_name" v-html="getSelectedLiveFeeds.home_team_name"></h1>
              <h1 v-else v-html="home_team_name"></h1>
              <p v-if="lineup.home">{{lineup.home.coach.length > 0 ? lineup.home.coach[0].lineup_player : ''}} - Coach</p>
            </div>
            <div v-if="lineup.home">
              <div v-for="(lineup, index) in lineup.home.starting_lineups" :key="index" class="line-up-card">
                <p><i>({{lineup.lineup_number}})</i> {{ lineup.lineup_player }} </p>
              </div>
            </div>
          </div>
          <div class="line-up-row">
            <div class="line-up-card br">
              <div v-if="getSelectedLiveFeeds.away_team_badge">
                <img :src="getSelectedLiveFeeds.away_team_badge" />
              </div>
              <div v-else>
                <img :src="away_logo" />
              </div>
              <h1 v-if="getSelectedLiveFeeds.away_team_name" v-html="getSelectedLiveFeeds.away_team_name"></h1>
              <h1 v-else v-html="away_team_name"></h1>
              <p v-if="lineup.away">{{lineup.away.coach.length > 0 ? lineup.away.coach[0].lineup_player : ''}} - Coach</p>
            </div>
            <div v-if="lineup.away">
              <div v-for="(lineup, index) in lineup.away.starting_lineups" :key="index" class="line-up-card">
                <p><i>({{lineup.lineup_number}})</i> {{ lineup.lineup_player }} </p>
              </div>
            </div>
          </div>
        </div>
        <div id="stats" class="tab-content text-center">
          <div v-if="statistics">
            <div class="statistics-line-bar" v-for="(stats,index) in statistics" :key="index">
              <span :class="stats.home > stats.away ? 'higher' : '' " v-html="stats.home"></span>
              <span v-html="stats.type"></span>
              <span :class="stats.away > stats.home ? 'higher-away' : '' " v-html="stats.away">47%</span>
            </div>
          </div>
          <div v-else>
            <p>Coming Soon</p>
          </div>
        </div>

      </div>


    </div>
  </div>
</template>

<script>
  import {mapState} from "vuex";
  import AllFeeds from "../../components/Home/allFeeds";

  export default {
    name: "match",
    components: {AllFeeds},
    data() {
      return {
        match_id: '',
        home_logo: "",
        away_logo: "",
        home_team_name: "",
        away_team_name: "",
        match_date: '',
        match_status: '',
        league_name: '',
        hometeam_score: '',
        awayteam_score: '',
        hometeam_halftime_score: '',
        awayteam_halftime_score: '',
        hometeam_extra_score: '',
        awayteam_extra_score: '',
        hometeam_penalty_score: '',
        awayteam_penalty_score: '',
        hometeam_ft_score: '',
        awayteam_ft_score: '',
        hometeam_system: '',
        awayteam_system: '',
        match_round: '',
        match_stadium: '',
        match_referee: '',
        goalscorer: {},
        cards: {},
        substitutions: {},
        lineup: {},
        statistics: {},
        reviews: {
          data: [],
          fullFeedsCount: '',
          next_page_url: '',
          last_page_url: '',
        },
        odds: {},
        feedsStatus: Boolean,
        spinnerData: {},
      }
    },
    computed: mapState({
      getSelectedLiveFeeds: state => state.selectedLiveFeed,
      getUserDetails: state => state.user,
    }),
    created() {
      this.match_id = this.$route.params.fixture_id
    },
    watch: {
      $route(to, from) {
        // react to route changes...
        this.fixture_id = to.params.fixture_id
        this.getMatchDetails(to.params.fixture_id)
      },
    },
    mounted() {
      // this.getMatchDetails(this.$route.params.match_id)
      // console.log('from selected live match', this.getSelectedLiveFeeds)
      this.tabPlugin();
      this.getMatchDetails()
      window.eventBus.$on('loadLiveMatch', val => {
        this.$router.replace('/match/' + val)
        this.getMatchDetails(val)
      })
      window.eventBus.$on('loadMoreReview', val => {
        const $route = this.$route.name
        if ($route === 'Match') {
          if(val === true) {
            const nextUrl = this.reviews.next_page_url
            this.addReview(nextUrl);
          }
        }
      })
    },
    methods: {
      formatDate(date) {
        const d = new Date(date);
        return d.toDateString();
      },
      tabPlugin() {
        // $('#tab-content div').hide();
        // $('#tab-content div:first').show();
        //
        // $('#nav li').click(function() {
        //     $('#nav li a').removeClass("active");
        //     $(this).find('a').addClass("active");
        //     $('#tab-content div').hide();
        //
        //     var indexer = $(this).index(); //gets the current index of (this) which is #nav li
        //     $('#tab-content div:eq(' + indexer + ')').fadeIn(); //uses whatever index the link has to open the corresponding box
        // });

        $(document).ready(function(){

          $('ul.tabs li').click(function(){
            var tab_id = $(this).attr('data-tab');

            $('ul.tabs li').removeClass('current');
            $('.tab-content').removeClass('current');

            $(this).addClass('current');
            $("#"+tab_id).addClass('current');
          })

        })


      },
      showLoading(options = {}) {
        let loader = this.spinnerData = {
          spinner: 'Jumper',
          isLoading: options.show !== undefined ? options.show : true ,
          color: '#00bfff',
          size: '60px',
          options: 'center relative',
        };
        return loader;
      },
      addReview(url) {
        let userId = 0;
        if (this.getUserDetails.id) {
          userId = this.getUserDetails.id;
        }
        window.eventBus.$emit('loadingFeed',true);
        this.axios
          .get(url)
          .then(res => {
            window.eventBus.$emit('loadingFeed',false);
            const match = res.data.data.match;
            this.home_team_name = match.home_team_name;
            this.away_team_name = match.away_team_name;
            this.home_logo = match.home_team_badge;
            this.away_logo = match.away_team_badge;
            this.match_date = match.match_date;
            this.league_name = match.league_name;
            this.match_status = match.match_status;
            this.hometeam_score = match.hometeam_score;
            this.awayteam_score = match.awayteam_score;
            this.hometeam_halftime_score = match.hometeam_halftime_score;
            this.awayteam_halftime_score = match.awayteam_halftime_score;
            this.hometeam_extra_score = match.hometeam_extra_score;
            this.awayteam_extra_score = match.awayteam_extra_score;
            this.hometeam_penalty_score = match.hometeam_penalty_score;
            this.awayteam_penalty_score = match.awayteam_penalty_score;
            this.hometeam_ft_score = match.hometeam_ft_score;
            this.awayteam_ft_score = match.awayteam_ft_score;
            this.hometeam_system = match.hometeam_system;
            this.awayteam_system = match.awayteam_system;
            this.match_round = match.match_round;
            this.match_stadium = match.match_stadium;
            this.match_referee = match.match_referee;
            this.goalscorer = match.goalscorer;
            this.cards = match.cards;
            this.substitutions = match.substitutions;
            this.lineup = match.lineup;
            this.statistics = match.statistics;
            if ( match.reviews.total > 0) {
              this.feedsStatus = true
            }else {
              this.feedsStatus = false
            }
            const matchD = match.reviews.data;
            // console.log(matchD)
            if (matchD !== undefined) {
              this.reviews.data.push(matchD);
            }
            const matchDetails = match.reviews

            this.reviews.fullFeedsCount = matchDetails.total;
            this.reviews.next_page_url = matchDetails.next_page_url;
            this.reviews.last_page_url = matchDetails.last_page_url;

            this.odds = match.odds
          })
      },
      getMatchDetails(key) {
        this.showLoading()
        if(!key) {
          key = this.$route.params.fixture_id
        }
        let userId = 0;
        if (this.getUserDetails.id) {
          userId = this.getUserDetails.id;
        }
        this.axios.get(`/matches/single/${key}/${userId}/100`)
                .then(res => {
                  this.showLoading({show:false})
                  // console.log(' live match details ',res.data)
                  const match = res.data.data.match;
                  this.home_team_name = match.home_team_name;
                  this.away_team_name = match.away_team_name;
                  this.home_logo = match.home_team_badge;
                  this.away_logo = match.away_team_badge;
                  this.match_date = match.match_date;
                  this.league_name = match.league_name;
                  this.match_status = match.match_status;
                  this.hometeam_score = match.hometeam_score;
                  this.awayteam_score = match.awayteam_score;
                  this.hometeam_halftime_score = match.hometeam_halftime_score;
                  this.awayteam_halftime_score = match.awayteam_halftime_score;
                  this.hometeam_extra_score = match.hometeam_extra_score;
                  this.awayteam_extra_score = match.awayteam_extra_score;
                  this.hometeam_penalty_score = match.hometeam_penalty_score;
                  this.awayteam_penalty_score = match.awayteam_penalty_score;
                  this.hometeam_ft_score = match.hometeam_ft_score;
                  this.awayteam_ft_score = match.awayteam_ft_score;
                  this.hometeam_system = match.hometeam_system;
                  this.awayteam_system = match.awayteam_system;
                  this.match_round = match.match_round;
                  this.match_stadium = match.match_stadium;
                  this.match_referee = match.match_referee;
                  this.goalscorer = match.goalscorer;
                  this.cards = match.cards;
                  this.substitutions = match.substitutions;
                  this.lineup = match.lineup;
                  this.statistics = match.statistics;
                  if ( match.reviews.data && match.reviews.data.length > 0) {
                    this.feedsStatus = true
                  }else {
                    this.feedsStatus = false
                  }
                  const matchD = match.reviews.data;
                  console.log(matchD)
                  if (matchD !== undefined) {
                    this.reviews.data.push(...matchD);
                  }
                  const matchDetails = match.reviews

                  this.reviews.fullFeedsCount = matchDetails.total;
                  this.reviews.next_page_url = matchDetails.next_page_url;
                  this.reviews.last_page_url = matchDetails.last_page_url;

                  this.odds = match.odds
                })
      },
    },
  }
</script>

<style>
  .scorers {
    width: 100%;
    padding: 10px;
  }
  .scorers p {
    font-weight: bold;
    text-shadow: 0 2px 8px rgba(0,0,0,.4);
    margin: 0;
  }
  .statistics-line-bar {
    padding: 5px 10px;
    width: 100%;
    float: left;
    border-bottom: solid 1px rgba(230, 236, 245, 0.4);
    display: flex;
    justify-content: space-between;
    color: #fff;
    margin-bottom: 10px;
  }
  .statistics-line-bar .higher {
    background-color: aquamarine;
    border-radius: 30px;
    padding: 6px 10px;
    color: black;
  }
  .statistics-line-bar .higher-away {
    background-color: #0ba4e0;
    border-radius: 30px;
    padding: 6px 10px;
  }
  .statistics-line-bar span {
    display: flex;
    align-items: flex-end;
  }
  .line-up-row {
    width: 50%;
    float: left;
    position: relative;
    padding-left: 10px;
    height: 100%;
  }
  .line-up-card {
    width: 100%;
    padding:10px;
    border-radius: 10px;
    float: left;
  }
  .line-up-card img {
    width: 60px;
    border-radius: 50%;
    margin-right: 10px;
    float: left;
  }
  .line-up-card p {
    float: left;
    margin:0;
    color: #d0d0d5;
  }
  .line-up-card p i {
    padding-right: 10px;
    font-style: normal;
    font-weight: bold;
  }
  .line-up-card h1 {
    margin:10px 0 0 0;
    font-size: 15px;
    color: #fff;
  }
  .live.liveScoresBox .caption h1 {
    color: #fff;
  }
  .live.liveScoresBox .caption p {
    color: #fff;
  }
  .live.liveScoresBox p {
    color: #fff;
  }
  .live .versus .match_options {
    color: #fff
  }

  ul.tabs{
    margin: 0px;
    padding: 3px;
    list-style: none;
    border-radius: 30px;
    display: flex;
    justify-content: space-between;
    background-color: rgb(208, 208, 213);
  }
  ul.tabs li{
    background: none;
    padding: 10px 15px;
    cursor: pointer;
    display: block;
    width: 100%;
    border-radius: 30px;
    text-align: center;
    color: #0b2e13;
  }

  ul.tabs li.current{
    background: #ededed;
    color: #0b2e13;
  }

  .content-tabs .tab-content{
    display: none;
    /*background: #ededed;*/
    padding: 15px;
    margin-top: 10px;
  }

  .tab-content.current{
    display: inherit;
  }
</style>
